body {
  margin: 0;
  padding: 0;
  color: #0e0e0e;
  min-width: 320px;
}

body.active {
  width: 100%;
  position: fixed;
}

html {
  box-sizing: border-box;
  overflow-y: scroll;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
}

.container {
  max-width: 1108px;
  margin: 0 auto;
}

@media (max-width: 1024px) {
  .container {
    width: 100%;
  }
}
.header {
  background-image: url(../img/bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
}
.header .header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
  margin-bottom: 10vw;
}
.header .header__nav .header__logo a svg path {
  fill: #000;
}
.header .header__nav .header__nav-list {
  display: flex;
  justify-content: space-between;
  width: 500px;
}
.header .header__nav .header__nav-list li {
  list-style: none;
}
.header .header__nav .header__nav-list li a {
  text-decoration: none;
  font-family: "Sofia Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  color: #000000;
  transition: 0.2s;
}
.header .header__nav .header__nav-list li a:hover {
  color: #f6d330;
}
.header .header__nav .header__nav-list .contact-us {
  display: none;
  margin: 0 auto;
  padding-left: 40px;
  align-items: center;
  text-decoration: none;
  width: 196px;
  height: 48px;
  background-color: #f9d023;
  font-family: "Sofia Pro";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0.6px;
  color: #0b0d21;
  position: relative;
  margin-top: 88px;
  margin-bottom: 100px;
}
.header .header__nav .header__nav-list .contact-us::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 10px;
  top: 18px;
  right: 30px;
  z-index: 1;
  background-image: url(../img/contact-us.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header .header__title {
  font-family: "Alata";
  font-style: normal;
  font-weight: 400;
  font-size: 115px;
  line-height: 130px;
  text-align: center;
  color: #ffffff;
}
.header .header__subtitle {
  font-family: "Sofia Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 35px;
  text-align: center;
  color: #ffffff;
  max-width: 950px;
  margin: 0 auto;
  margin-bottom: 80px;
}
.header .header__link {
  display: flex;
  margin: 0 auto;
  padding-left: 40px;
  align-items: center;
  text-decoration: none;
  width: 196px;
  height: 48px;
  background-color: #f9d023;
  font-family: "Sofia Pro";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0.6px;
  color: #0b0d21;
  position: relative;
  margin-bottom: 227px;
}
.header .header__link::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 10px;
  top: 18px;
  right: 30px;
  z-index: 1;
  background-image: url(../img/contact-us.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header .header__link-more {
  display: block;
  width: 93px;
  margin: 0 auto;
  font-family: "Sofia Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  position: relative;
  text-decoration: none;
  padding-bottom: 50px;
}
.header .header__link-more::before {
  position: absolute;
  content: "";
  top: -130px;
  left: 50px;
  z-index: 1;
  z-index: 1;
  width: 1px;
  height: 101px;
  background: #f9d023;
}

@media (max-width: 1024px) {
  .header .header__nav {
    padding: 0 20px;
  }
  .header .header__nav .burger__menu {
    cursor: pointer;
    display: block;
    width: 25px;
    height: 25px;
    position: relative;
  }
  .header .header__nav .burger__menu::before {
    position: absolute;
    content: "";
    top: 7px;
    left: 0;
    z-index: 1;
    background-color: #000000;
    width: 100%;
    height: 2px;
  }
  .header .header__nav .burger__menu::after {
    position: absolute;
    content: "";
    top: 16px;
    left: 0;
    z-index: 1;
    background-color: #000000;
    width: 100%;
    height: 2px;
  }
  .header .header__nav .burger__menu.active {
    cursor: pointer;
  }
  .header .header__nav .burger__menu.active::before {
    top: 10px;
    transform: rotate(45deg);
    background-color: #fff;
  }
  .header .header__nav .burger__menu.active::after {
    top: 10px;
    transform: rotate(-45deg);
    background-color: #fff;
  }
  .header .header__nav .header__nav-list {
    position: fixed;
    top: -1000px;
    left: 0;
    z-index: 10;
    flex-direction: column;
    width: 100%;
    background-color: #fff;
  }
  .header .header__nav .header__nav-list li:first-child {
    padding-top: 50px;
  }
  .header .header__nav .header__nav-list li {
    margin: 0 auto;
    margin-bottom: 24px;
  }
  .header .header__nav .header__nav-list li a {
    font-family: "Alata";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 48px;
    color: #000000;
  }
  .header .header__nav .header__nav-list .contact-us {
    display: flex;
  }
  .header .header__nav .header__nav-list.active {
    top: 70px;
  }
  .header .header__nav.active {
    background-color: #000000;
  }
  .header .header__nav.active .header__logo a svg path {
    fill: #fff;
  }
  .header .header__title {
    font-size: 90px;
  }
  .header .header__subtitle {
    font-size: 20px;
    max-width: 785px;
  }
}
@media (max-width: 786px) {
  .header .header__title {
    font-size: 70px;
  }
  .header .header__subtitle {
    font-size: 18px;
    max-width: 640px;
  }
}
@media (max-width: 621px) {
  .header .header__title {
    font-size: 40px;
    font-weight: 400;
    font-size: 50px;
    line-height: 60px;
  }
  .header .header__subtitle {
    font-size: 16px;
    max-width: 540px;
  }
  .header .header__link-more::before {
    display: none;
  }
}
.anchor__nav {
  position: fixed;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  z-index: 1;
}
.anchor__nav .anchor__nav-list .anchor__nav-list-item {
  list-style: none;
  margin-bottom: 24px;
  padding-right: 30px;
  text-align: right;
  position: relative;
}
.anchor__nav .anchor__nav-list .anchor__nav-list-item:before {
  position: absolute;
  content: "";
  top: 12px;
  right: 0;
  z-index: 1;
  width: 20px;
  height: 1px;
  background-color: #fff;
}
.anchor__nav .anchor__nav-list .anchor__nav-list-item a {
  text-decoration: none;
  font-family: "Sofia Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #ffffff;
}
.anchor__nav .anchor__nav-list .anchor__nav-list-item.active {
  padding-right: 70px;
  position: relative;
}
.anchor__nav .anchor__nav-list .anchor__nav-list-item.active:before {
  position: absolute;
  content: "";
  top: 12px;
  right: 0;
  z-index: 1;
  width: 60px;
  height: 1px;
  background-color: #fff;
}

@media (max-width: 1024px) {
  .anchor__nav {
    display: none;
  }
}